import {FormControl, FormControlLabel, FormGroup, FormLabel, Grid, Rating, TextField} from "@mui/material";

export default function PitForm() {
    return (
            <form>
                <FormControl variant={'outlined'} component={'fieldset'}>
                    <FormLabel>Pit Scouting Form</FormLabel>
                    <FormGroup>
                        <FormControlLabel control={<TextField variant={'outlined'}/>} label={'Team Number'}/>
                        <FormControlLabel control={<Rating
                            name="TeamRating"
                        />} label={'Team Rating'}/>
                    </FormGroup>
                </FormControl>
            </form>
    );
}